import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import Post from '../components/Post';
import Pagination from '../components/Pagination';
import { Container, Row } from 'react-bootstrap';

const Updates = () => {
  const [ updates, setUpdates ] = useState([]);
  const [ count, setCount ] = useState(0);
  const [ previous, setPrevious ] = useState('');
  const [ next, setNext ] = useState('');
  const [ active, setActive ] = useState(1);

  useEffect(() => {
    window.scrollTo(0,0);

    const fetchData = async() => {
      try {
        const res = await axios.get('http://heinsch.me/api/updates/?page=1');

        setUpdates(res.data.results);
        setCount(res.data.count);
        setPrevious(res.data.previous);
        setNext(res.data.next);
      } catch (err) {

      }
    }

    fetchData();
  }, []);

  const displayUpdates = () => {
    let display = [];
    let result = [];

    updates.map(post => {
      return display.push(
        <Post
          title={post.title}
          content={post.content}
          date_posted={post.date_posted}
        />
      );
    });

    for (let i = 0; i < updates.length; i += 10) {
      result.push(
        <div key={i}>
          <Container>
            <Row>
              {display[i]}
            </Row>
            <Row>
              {display[i+1] ? display[i+1] : null}
            </Row>
            <Row>
              {display[i+2] ? display[i+2] : null}
            </Row>
            <Row>
              {display[i+3] ? display[i+3] : null}
            </Row>
            <Row>
              {display[i+4] ? display[i+4] : null}
            </Row>
            <Row>
              {display[i+5] ? display[i+5] : null}
            </Row>
            <Row>
              {display[i+6] ? display[i+6] : null}
            </Row>
            <Row>
              {display[i+7] ? display[i+7] : null}
            </Row>
            <Row>
              {display[i+8] ? display[i+8] : null}
            </Row>
            <Row>
              {display[i+9] ? display[i+9] : null}
            </Row>
          </Container>
        </div>
      );
    }

    return result;
  };

  const visitPage = (page) => {
    axios.get(`http://localhost:8000/api/updates/?page=${page}`)
    .then(res => {
      setUpdates(res.data.results);
      setPrevious(res.data.previous);
      setNext(res.data.next);
      setActive(res.data.page);
    })
    .catch(err => {

    });
  };

  const previous_number = () => {
    axios.get(previous)
    .then(res => {
      setUpdates(res.data.results);
      setPrevious(res.data.previous);
      setNext(res.data.next);
      if (previous) {
        setActive(active - 1);
      }
    })
    .catch(err => {

    });
  };

  const next_number = () => {
    axios.get(next)
    .then(res => {
      setUpdates(res.data.results);
      setPrevious(res.data.previous);
      setNext(res.data.next);
      if (next) {
        setActive(active + 1);
      }
    })
    .catch(err => {

    });
  };

  return (
    <div className='updates'>
      <Helmet>
        <title>My Portfolio - Updates</title>
        <meta
          name='Updates Page'
          content='These are my update posts for my portfolio site'
        />
      </Helmet>
      <div className='content-section updates__pagination'>
        <Pagination
          itemsPerPage={10}
          count={count}
          visitPage={visitPage}
          previous={previous_number}
          next={next_number}
          active={active}
          setActive={setActive}
        />
      </div>
      <div className='updates__updates'>
        {displayUpdates()}
      </div>
      <div className='content-section updates__pagination'>
        <Pagination
          itemsPerPage={10}
          count={count}
          visitPage={visitPage}
          previous={previous_number}
          next={next_number}
          active={active}
          setActive={setActive}
        />
      </div>
    </div>
  );
};


export default Updates;
