import React, { Fragment } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';
import Alert from './Alert';
import PropTypes from 'prop-types';

import { Container, Col, Navbar, Nav, NavItem, NavDropdown } from 'react-bootstrap';

const navbar = ({ auth:{ isAuthenticated, loading }, logout}) => {
  const authLinks = (
    <Fragment>
      <li className="nav-item">
        <a className="nav-link" onClick={logout} href="#!">Logout</a>
      </li>
    </Fragment>
  );

  const guestLinks = (
    <Fragment>
      <li className="nav-item">
        <Nav.Link as={Link} exact to="/login">Login</Nav.Link>
      </li>
      <li className="nav-item">
        <Nav.Link as={Link} exact to="/signup">Signup</Nav.Link>
      </li>
    </Fragment>
  );

  return (
    <>
      <Navbar collapseOnSelect className='navbar darker' expand="lg" fixed="top" variant="dark">
        <Navbar.Brand as={Link} exact to="/">My Portfolio</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} exact to="/">Home</Nav.Link>
            <Nav.Link as={Link} exact to="/projects">Projects</Nav.Link>
            <Nav.Link as={Link} exact to="/surveys">Surveys</Nav.Link>
            <Nav.Link as={Link} exact to="/updates">Updates</Nav.Link>
            <Nav.Link as={Link} exact to="/about">About</Nav.Link>
            <Nav.Link
              href="https://github.com/Udantu"
              target="_blank"
              rel="noopener noreferrer"
              onclick="window.open('https://github.com/Udantu')">
              Source Code
            </Nav.Link>
          </Nav>
          <Nav>
            { !loading && (<Fragment>{ isAuthenticated ? authLinks : guestLinks }</Fragment>)}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};

// <NavDropdown title="Apps" id="collasible-nav-dropdown">
//   <NavDropdown.Item as={Link} exact to="#action/3.1">Action</NavDropdown.Item>
//   <NavDropdown.Item as={Link} exact to="#action/3.2">Another action</NavDropdown.Item>
//   <NavDropdown.Item as={Link} exact to="#action/3.3">Something</NavDropdown.Item>
//   <NavDropdown.Divider />
//   <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
// </NavDropdown>

navbar.propTypes = {
  logout: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(mapStateToProps, { logout })(navbar);
