import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import csc356 from "../html/compro_csc356.pdf";
import sta257 from "../html/compro_sta257.pdf";
import resume from "../assets/files/Resume_DanHeinsch_May2021.pdf";

import { Container, Badge, Button, Breadcrumb } from 'react-bootstrap';

const ProjectDetail = (props) => {
  const [project, setProject] = useState({});
  const [t, setT] = useState([]);
  let tags = [];
  let pdf = ''

  const formatTags = (formattedtags) => {
    formattedtags = formattedtags + ''
    formattedtags = formattedtags.split(',');
    let tagSpan = []
    for (const tag in formattedtags){
      tagSpan.push(<><Badge pill variant="secondary">{formattedtags[tag]}</Badge>{' '}</>);
    }
    return tagSpan
  }

  const getPDF = (file) => {
    switch(file) {
      case "csc356":
        return csc356
      case "sta257":
        return sta257
      default:
        return "error"
    }
  }

  const fileLink = (file) => {
    pdf = getPDF(file)
    return(
      <>
        <Button
          className='projectdetail__url'
          variant="light"
          target="_blank"
          rel="noopener noreferrer"
          href={pdf}>
          Source
        </Button>
      </>
  )};

  const urlLink = (
    <Button
      className='projectdetail__url'
      variant="light"
      target="_blank"
      rel="noopener noreferrer"
      href={project.url}>
      Source
    </Button>
  );



  useEffect(() => {
    const id = props.match.params.id;

    const config = {
      headers: {
        'Content-Type':'application/json'
      }
    };
    axios.get(`http://heinsch.me/api/projects/${id}`, config)
    .then(res => {
      setProject(res.data);
    })
    .catch(err => {

    });
  }, [props.match.params.id]);

  return(
    <>
    <Breadcrumb>
      <Breadcrumb.Item as={Link} to="/">
        <Link to='/' style={{ textDecoration: 'none' }}>
          Home
        </Link>
      </Breadcrumb.Item>
      <Breadcrumb.Item>
        <Link to='/projects' style={{ textDecoration: 'none' }}>
          Projects
        </Link>
      </Breadcrumb.Item>
      <Breadcrumb.Item active>Project: {project.title}</Breadcrumb.Item>
    </Breadcrumb>

    <Container className='content-section projectdetail'>
      <h1 className='projectdetail__title'>{project.title}</h1>
      <hr />
      <p className='projectdetail__description'>{project.description}</p>
      <hr />
      <div className='projectdetail__tags'>
        { tags = formatTags(project["tag"])}
        { tags }
      </div>
      <hr />
      <Container className='text-center'>
        { project.srcType == "URL" ? urlLink : fileLink(project["file"]) }
      </Container>
    </Container>
    </>
  );
};


export default ProjectDetail;
