import React, { useEffect, useState } from 'react';
import axios from 'axios';

import PropTypes from 'prop-types';
import Question from '../components/Question';

import { Container, Row, Col, Form, Button } from 'react-bootstrap';

axios.defaults.xsrfCookieName = 'csrftoken'
axios.defaults.xsrfHeaderName = 'X-CSRFToken'

const SurveyDetail = (props) => {
  const [questions, setQuestions] = useState([]);

  // useEffect(() => {
  //   const id = props.match.params.id;
  //
  //   const config = {
  //     headers: {
  //       'Content-Type':'application/json'
  //     }
  //   };
  //   axios.get(`http://localhost:8000/api/surveys/${id}`, config)
  //   .then(res => {
  //     setQuestions(res.data);
  //     //console.log(questions.title);
  //   })
  //   .catch(err => {
  //
  //   });
  // }, [props.match.params.id]);
  useEffect(() => {

    const id = props.match.params.id;
    window.scrollTo(0,0);

    const fetchData = async() => {
      try {
        const res = await axios.get(`http://heinsch.me/api/surveys/${id}`);
        let resdata = res.data.results;
        var resdataFiltered = resdata.filter( element => element.survey ==`${id}`)
        setQuestions(resdataFiltered);
      } catch (err) {
      }
    }
    fetchData();
  }, []);


  const displayQuestions = () => {
    let display = [];
    let result = [];

    questions.map(question => {
      return display.push(
        <>
          <Question
            question={question.question}
            qNumber={question.qNumber}
          />
        </>
      );
    });

    for (let i = 0; i < questions.length; i += 1) {
      result.push(
        <div key={i}>
          <div>
            {display[i] ? display[i] : null}
          </div>
          <hr />
        </div>
      );
    }

    return result;
  };

  return(
    <div className='content-section'>
      <div className='row'>
        <Col xs={10}>
          <h2>Survey</h2>
        </Col>
      </div>
      <hr />
      <Form>
        {displayQuestions()}
        <div className='center'>
        <Button variant="dark" type="submit">
          Submit
        </Button>
        </div>
      </Form>
    </div>
  );
};


export default SurveyDetail;
