import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import moment from 'moment';

const post = (props) => {
  // TODO
  const formatTime = (t) => {
    return moment(t).format('MMMM Do, YYYY');
  };

  return (
    <div className='post container content-section'>
      <div>
        <div>
          <small className="text-muted">{formatTime(props.date_posted)}</small>
        </div>
        <hr />
        <h2>{props.title}</h2>
        <p>{props.content}</p>
      </div>
    </div>
  );
};

post.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  date_posted: PropTypes.string.isRequired,
}

export default post;
