import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import { Container, Row, Badge, Button, Col } from 'react-bootstrap';

const programCard = (props) => {
  const formatTags = (tags) => {
    let arr = tags.split(',');
    let tagSpan = [];
    for (const tag in tags){
      tagSpan.push(<><Badge pill variant="secondary">{arr[tag]}</Badge>{' '}</>);
    }
    return tagSpan;
  };

  return (
    <Col className='container programcard'>
      <Link to={`/projects/${props.id}`} style={{ textDecoration: 'none' }}>
        <div className='programcard__body'>
          <strong className='programcard__body__title'>{props.title}</strong>
          <hr />
          <p className="programcard__body__tags">{formatTags(props.tags)}</p>
        </div>
      </Link>

    </Col>
  );
};

programCard.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  tags: PropTypes.string.isRequired,
}

export default programCard;
