import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Home from './containers/Home';
import About from './containers/About';
import Projects from './containers/Projects';
import ProjectDetail from './containers/ProjectDetail';
import Surveys from './containers/Surveys';
import SurveyDetail from './containers/SurveyDetail';
import Dashboard from './containers/Dashboard';
import Updates from './containers/Updates';
import Login from './containers/Login';
import SignUp from './containers/SignUp';
import Profile from './containers/Profile';
import NotFound from './components/NotFound';
import Layout from './hocs/Layout';
import PrivateRoute from './components/privateRoute';

import { Provider } from 'react-redux';
import store from './store';

import './sass/main.scss';

const App = () => (
  <Provider store={store}>
    <Router>
      <Layout>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/about' component={About} />
          <Route exact path='/projects' component={Projects} />
          <Route exact path='/projects/:id' component={ProjectDetail} />
          <Route exact path='/surveys' component={Surveys} />
          <PrivateRoute exact path='/surveys/:id' component={SurveyDetail} />
          <Route exact path='/dashboard' component={Dashboard} />
          <Route exact path='/updates' component={Updates} />
          <Route exact path='/login' component={Login} />
          <Route exact path='/signup' component={SignUp} />
          <Route exact path='/profile/:id' component={Profile} />
          <Route component={NotFound} />
        </Switch>
      </Layout>
    </Router>
  </Provider>
);
//<Route exact path="/projects/:id/:title" render={() => {window.location.href="../html/compro_csc356.html"}} />
export default App;
