import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { Container, Row, Badge, Button, Col } from 'react-bootstrap';

const surveyCard = (props) => {
  console.log("props.id /surveyCard = ",props.id);
  return (
    <Container>
      <div className='content-section'>
        <div className='surveycard'>
          <div>
            <h3 className='surveycard__title'>{props.title}</h3>
            <hr />
            <p className="surveycard__title">{props.description}</p>
          </div>
          <div className='text-center'>
            <Button as={Link} className='surveycard__button' variant='dark' to={`/surveys/${props.id}`}>{'>'}</Button>
          </div>
        </div>
      </div>
    </Container>
  );
};


surveyCard.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
}

export default surveyCard;
