import React, { useState }from 'react';
import axios from 'axios';
import Loader from 'react-loader-spinner';
import PropTypes from 'prop-types';

import { Container, Row, Col, InputGroup } from 'react-bootstrap';

const ProjectSearch = (props) => {
  const [projectData, setProjectData] = useState({
    keywords: ''
  });

  const { keywords } = projectData;

  const [loading, setLoading] = useState(false);

  const onChange = e => setProjectData({...projectData, [e.target.name]: e.target.value});

  const onSubmit = e => {
    e.preventDefault();

    axios.default.headers = {
      "Content-Type":"application/json"
    };

    setLoading(true);

    axios.post("http://heinsch.me/api/projects/search", { keywords })
    .then( res => {
      setLoading(false);
      props.setProjects(res.data);
      window.scrollTo(0,0);
    })
    .catch(err => {
      setLoading(false);
      window.scrollTo(0,0);
    })
  };

  return (
    <Container className='projectform container'>
      <form className='projectform__form' onSubmit={e => onSubmit(e)}>
      <Row>
        <Col>
          <div className='projectform__form__search center'>
            <input className='projectform__form__search__input' name='keywords' placeholder='Search...' type='text' onChange={e => onChange(e)} value={keywords} />
          </div>
          {loading ?
            <div className='projectform__form__loader'>
              <Loader
                type='Rings'
                color='#424242'
                height={50}
                width={50}
              />
            </div> :
            <div className='Button below goes here!'></div>
          }
        </Col>
      </Row>
      </form>
    </Container>
  );
};

// <button className='projectform__form__button projectform__form__button--primary'><span>{' >> '}</span></button>

ProjectSearch.propTypes = {
  setProjects: PropTypes.func.isRequired
};

export default ProjectSearch;
