import React, { useEffect, useState } from 'react';
import axios from 'axios';
import SurveyCard from '../components/SurveyCard';
import Helmet from 'react-helmet';

import { Container, Row } from 'react-bootstrap';

const Surveys = () => {
  const [ surveys, setSurveys ] = useState([]);
  useEffect(() => {
    window.scrollTo(0,0);

    const fetchData = async() => {
      try {
        const res = await axios.get('http://heinsch.me/api/surveys/');

        setSurveys(res.data.results);
      } catch (err) {
      }
    }
    fetchData();
  }, []);

  const displaySurveys = () => {
    let display = [];
    let result = [];
    surveys.map(survey => {
      return display.push(
        <>
          <SurveyCard
            title={survey.title}
            description={survey.description}
            id={survey.id}
          />
        </>
      );
    });

    for (let i = 0; i < surveys.length; i += 1) {
      result.push(
        <React.Fragment key={i}>
          {display[i] ? display[i] : null}
        </React.Fragment>
      );
    }

    return result;
  };

  return(
    <>
      <Helmet>
        <title>My Portfolio - Surveys</title>
        <meta
          name='Surveys Page'
          content='These are my surveys for my portfolio site'
        />
      </Helmet>
      <Row>
        {displaySurveys()}
      </Row>
    </>
  );
};


export default Surveys;
